import React, { useEffect, useState } from "react";
import * as XLSX from 'xlsx';  //   
import { fetchDonors } from "../../services/donor.service";  //  API

const DonorsList = () => {
    const [donors, setDonors] = useState<any[]>([]);  //     
    const [filteredDonors, setFilteredDonors] = useState<any[]>([]);  //    
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [searchQuery, setSearchQuery] = useState<string>("");  //   

    useEffect(() => {
        const loadDonors = async () => {
            try {
                const data = await fetchDonors(); //   
                setDonors(data);
                setFilteredDonors(data);  //       
            } catch (err) {
                setError("  ");
            } finally {
                setLoading(false);
            }
        };

        loadDonors();
    }, []);
    const formatDate = (isoDate:any) => {
        console.log(isoDate)
        const date = new Date(isoDate);
        console.log(date.toLocaleDateString("he-IL"))

        return date.toLocaleDateString("he-IL");  //   
    };
    const handleExportToExcel = () => {
        const ws = XLSX.utils.json_to_sheet(filteredDonors);  //   
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "Donors");
        XLSX.writeFile(wb, "donors.xlsx");  //   
    };

    const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchQuery(e.target.value);  //   
        const filtered = donors.filter(donor =>
            `${donor.first_name} ${donor.last_name}`
                .toLowerCase()
                .includes(e.target.value.toLowerCase())  //    
        );
        setFilteredDonors(filtered);  //   
    };

    if (loading) return <p> ...</p>;
    if (error) return <p style={{ color: "red" }}>{error}</p>;

    return (
        <div>
            <h2>  </h2>

            <input
                type="text"
                placeholder="   "
                value={searchQuery}
                onChange={handleSearch}
                style={{ marginBottom: "20px", padding: "5px", width: "100%" }}
            />

            <button onClick={handleExportToExcel} style={{ marginBottom: "20px" }} className="donate-button">
                 
            </button>

            <table className="table table-bordered table-striped table-hover">
                <thead className="thead-dark">
                    <tr>
                        <th> </th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th> </th>
                    </tr>
                </thead>
                <tbody>
                    {filteredDonors.length > 0 ? (
                        filteredDonors.map((donor) => (
                            <tr key={donor.id}>
                                <td>{donor.first_name} {donor.last_name}</td>
                                <td>{donor.phone || " "}</td>
                                <td>{donor.email}</td>
                                <td>{donor.address || " "}</td>
                                <td>{donor.createdat}</td>
                            </tr>
                        ))
                    ) : (
                        <tr>
                            <td colSpan={5} className="text-center">  </td>
                        </tr>
                    )}
                </tbody>
            </table>
        </div>
    );
};

export default DonorsList;
