import React, { useEffect, useState } from "react";
import * as XLSX from 'xlsx';  //   
import { DonationFromServise } from '../../models/DonationFromServise'
import { fetchDonations } from "../../services/donationsService";

const DonationsList = () => {
    const [donations, setDonations] = useState<DonationFromServise[]>([]);
    const [filteredDonations, setFilteredDonations] = useState<DonationFromServise[]>([]);  //  
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [searchQuery, setSearchQuery] = useState<string>("");  //   

    useEffect(() => {
        const loadDonations = async () => {
            try {
                const data = await fetchDonations(); //   
                setDonations(data);
                setFilteredDonations(data);  //       
            } catch (err) {
                setError("  ");
            } finally {
                setLoading(false);
            }
        };

        loadDonations();
    }, []);

    const handleExportToExcel = () => {
        const ws = XLSX.utils.json_to_sheet(filteredDonations);  //   
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "Donations");
        XLSX.writeFile(wb, "donations.xlsx");  //   
    };

    const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchQuery(e.target.value);  //   
        const filtered = donations.filter(donation =>
            `${donation.first_name} ${donation.last_name}`
                .toLowerCase()
                .includes(e.target.value.toLowerCase())  //    
        );
        setFilteredDonations(filtered);  //   
    };

    if (loading) return <p> ...</p>;
    if (error) return <p style={{ color: "red" }}>{error}</p>;

    return (
        <div>
            <h2>  </h2>

            <input
                type="text"
                placeholder="   "
                value={searchQuery}
                onChange={handleSearch}
                style={{ marginBottom: "20px", padding: "5px", width: "100%" }}
            />

            <button onClick={handleExportToExcel} style={{ marginBottom: "20px" }} className="donate-button">
                 
            </button>

            <table className="table table-bordered table-striped table-hover">
                <thead className="thead-dark">
                    <tr>
                        <th> </th>
                        <th></th>
                        <th> </th>
                        <th></th>
                        <th> </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {filteredDonations.length > 0 ? (
                        filteredDonations.map((donation) => (
                            <tr key={donation.id}>
                                <td>{donation.first_name} {donation.last_name}</td>
                                <td>{donation.amount} </td>
                                <td>{new Date(donation.donation_date).toLocaleDateString()}</td>
                                <td>{donation.frequency === "one-time" ? "-" : ""}</td>
                                <td>{donation.purpose || " "}</td>
                                <td>{donation.notes || ""}</td>
                            </tr>
                        ))
                    ) : (
                        <tr>
                            <td colSpan={6} className="text-center">  </td>
                        </tr>
                    )}
                </tbody>
            </table>
        </div>
    );
};

export default DonationsList;
