import React, { FC, useState } from 'react';
import './AddAnnouncement.scss';
import { createAnnouncement } from '../../services/announcementService'; //    -API

interface AddAnnouncementProps {}

const AddAnnouncement: FC<AddAnnouncementProps> = () => {
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [startDate, setStartDate] = useState<Date | null>(null);
  const [endDate, setEndDate] = useState<Date | null>(null);
  const [isActive, setIsActive] = useState(true);

  //    -API
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      if (!startDate || !endDate) {
        alert('    ');
        return;
      }

      const newAnnouncement = {
        title,
        content,
        startDate: startDate.toISOString(), //    ISO
        endDate: endDate.toISOString(),     //    ISO
        isActive,
      };
      await createAnnouncement(newAnnouncement); //     
      alert("  !");
      //    
      setTitle('');
      setContent('');
      setStartDate(null);
      setEndDate(null);
      setIsActive(true);
    } catch (error) {
      console.error("Error creating announcement", error);
      alert("   .");
    }
  };

  return (
    <div className="AddAnnouncement">
      <section className="announcement-form">
        <h2>  </h2>
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="title">:</label>
            <input
              type="text"
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="content"> :</label>
            <textarea
              id="content"
              value={content}
              onChange={(e) => setContent(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="startDate"> :</label>
            <input
              type="date"
              id="startDate"
              value={startDate ? startDate.toISOString().split('T')[0] : ''}
              onChange={(e) => setStartDate(e.target.value ? new Date(e.target.value) : null)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="endDate"> :</label>
            <input
              type="date"
              id="endDate"
              value={endDate ? endDate.toISOString().split('T')[0] : ''}
              onChange={(e) => setEndDate(e.target.value ? new Date(e.target.value) : null)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="isActive"> :</label>
            <input
              type="checkbox"
              id="isActive"
              checked={isActive}
              onChange={() => setIsActive(!isActive)}
            />
          </div>

          <div className="form-group">
            <button type="submit" className="cta-button"> </button>
          </div>
        </form>
      </section>
    </div>
  );
};

export default AddAnnouncement;
